import  { Link } from "react-router-dom";

const ProductCard = (props) => {
    let {id, name, thumbnail, price, region} = props.data;
    let useClass = "category";
    if (!region) {
        useClass += " hide";
    }
    return (
        <article className="col">
            <Link className="product-card" to={`/our-coffee/${id}`}>
                <div className="card-img">
                    <img src={thumbnail} alt={name}/>
                </div>
                <h2>{name}</h2>
                <span className={useClass}>{region}</span>
                <strong className="price">${price}</strong>
            </Link>
        </article>
    );
}

export default ProductCard;