import ProductCard from "../productCard/ProductCard";

const Products = ({data}) => {
    const products = data.map(item => {
        let{id} = item;
        return (
            <ProductCard key={id} data={item}/>
        )}
    )
    return (
        <div className="products-holder">
            <div className="row">
                {products}
            </div>
        </div>
    );
}

export default Products;