const MainInfo = ({data, text}) => {
    let {thumbnail, name, heading} = data;

    if(!heading) {
        heading = name;
    }

    return (
        <section className="about">
            <div className="container">
                <div className="row">
                    <div className="img-block">
                        <img src={thumbnail} alt={name}/>
                    </div>
                    <div className="text-block">
                        <h1 className="decor">{heading}</h1>
                        {text}
                    </div>
                </div>
            </div>
        </section>
    );
}


export default MainInfo;