import  { NavLink } from "react-router-dom";

const Nav = (props) => {
    const {onCloseMobileNav, navActiveClass} = props;
    return (
        <nav>
            <ul className="menu">
                <li><NavLink onClick={() => onCloseMobileNav(navActiveClass)} to="/">Home</NavLink></li>
                <li><NavLink onClick={() => onCloseMobileNav(navActiveClass)} to="/our-coffee">Our coffee</NavLink></li>
                <li><NavLink onClick={() => onCloseMobileNav(navActiveClass)} to="/contact-us">Contact us</NavLink></li>
            </ul>
        </nav>
    )
}

export default Nav;