import { useState } from "react";
import Logo from "../logo/Logo";
import Nav from "../menu/Menu";

const Header = () => {
    const [navActive, setNavActive] = useState(false);

    const mobileNav = (activeClass) => {
      openMobileNav(activeClass);
      closeMobileNav(activeClass);
    }

    const openMobileNav = (activeClass) => {
      if (!activeClass) {
        document.body.classList.add('nav-active');
        setNavActive(true);
      }
    }

    const closeMobileNav = (activeClass) => {
      if (activeClass) {
        document.body.classList.remove('nav-active');
        setNavActive(false);
      }
    }

    return (
        <header className="header">
            <div className="container">
                <div className="row">
                    <Logo navActiveClass={navActive} onCloseMobileNav={closeMobileNav} color={true}/>
                    <button onClick={() => mobileNav(navActive)} className="nav-opener"><span></span></button>
                    <div className="drop">
                        <Nav navActiveClass={navActive} onCloseMobileNav={closeMobileNav}/>
                    </div>
                </div>
            </div>
        </header>
        );
    }
  
  export default Header;