import { useState, useCallback } from "react";

const useSearchProducts = () => {
    const [process, setProcess] = useState('waiting');

    const getProducts = useCallback(async (url = "https://fake-coffee-api.vercel.app/api", method = "GET", body = null, headers = {"Content-Type": "application/json"}) => {
        setProcess('loading');

        try {
            const response = await fetch(url, {method, body, headers});

            if (!response.ok) {
                throw new Error(`Could not fetch ${url}, status: ${response.status}`);
            }

            const data = await response.json();
            const results = data.map(_transformProduct);
            return results;

        } catch(e) {
            setProcess('error');
            throw e;
        }
  
    }, []);

    const clearError = useCallback(() => {
        setProcess('loading');
    }, []);

    const _transformProduct = (products) => {
		return {
			id: products.id,
            pageId: products._id,
			name: products.name,
			description: products.description,
            price: products.price,
            region: products.region,
            weight: products.weight,
            flavor: products.flavor_profile,
			thumbnail: products.image_url
		};
	};

    return {getProducts, clearError, setProcess, process};
}

export default useSearchProducts;
