const Intro = (props) => {
    let {src, useClass} = props;
    if(!useClass) {
        useClass="";
    }
    return (
        <section className={`intro bg-img${useClass}`} style={{backgroundImage: `url(${src})`}}>
            <div className="container">
                {props.children}
            </div>
        </section>
    );
}


export default Intro;