import  { Link } from "react-router-dom";

import logo from '../../images/logo.svg';

const Logo = ({onCloseMobileNav, navActiveClass}) => {

    return (
        <strong className="logo">
            <Link onClick={onCloseMobileNav && navActiveClass ? () => onCloseMobileNav(navActiveClass) : null} to="/">
                <img src={logo} alt="Coffee house"/>
            </Link>
        </strong>
    )
}

export default Logo;